h1.title, span.title {
	font-family: 'Assistant', sans-serif; }

.drop-shadow {
	text-shadow: 8px 5px 4px rgba(0, 0, 0, 0.4); }

.drop-shadow-light {
	text-shadow: 3px 3px 2px rgba(0, 0, 0, 0.2); }

.is-huge {
	font-size: 9vw;
	& + .is-huge {
		margin-top: -.4em; } }

.has-text-spaced {
    word-spacing: 10px; }

.code-snip, pre {
	background-color: transparent; }

.header-content {
	padding-left: 3vw;
	padding-right: 3vw;
	max-height: 400px;
	overflow: hidden; }

.hero-right {
	filter: blur(1px); }

hr.small-divider {
	// background-color: $yellow
	max-width: 300px;
	margin: 10px auto; }

.astrodivider {
	margin: 35px auto;
	margin-bottom: 67px;
	width: 400px;
	max-width: 100%;
	position: relative;

	span {
		width: 50px;
		height: 50px;
		position: absolute;
		bottom: 100%;
		margin-bottom: -25px;
		left: 50%;
		margin-left: -25px;
		border-radius: 100%;
		box-shadow: 0 2px 4px $yellow;
		border: 3px solid $yellow;
		background: #fff; }

	i {
		position: absolute;
		top: 4px;
		bottom: 4px;
		left: 4px;
		right: 4px;
		border-radius: 100%;
		text-align: center;
		line-height: 40px;
		font-style: normal;
		color: #049372; } }

#visualization {
	margin: auto;
	min-width: 75vw;
	min-height: 75vw; }

a {
	color: white;
	// .icon.is-link
	&:hover, &:focus, &:active {
		cursor: pointer;
		color: $yellow;
		transition-duration: 0.2s;
		// transition-property: transform;
 } }		// transform: scale(1.3) translateZ(0);

