p.paragraph {
	text-indent: 4em;
	font-size: 1.1em;
	text-align: justify;
	&::first-letter {
	    font-size: 1.7em;
	    line-height: 1em; }
	a {
		color: $turquoise;
		&:hover {
			text-decoration: underline; } } }



.navbar-item {
	font-weight: bold; }

.columns.vertical-align {
	align-items: center; }

.scroll-content-down {
    animation: moveDown 500s linear infinite, fadeIn 4s linear forwards; }

span.icon {
	&.is-spaced {
		margin-bottom: 10px; }
	&+div {
		padding-top: 6px; } }

ul {
	color: $turquoise;
	padding: 10px 0;
	li {
		padding: 2px 0;

		b {
		    margin-bottom: 10px;
		    display: inline-block;
		    font-size: 1.2em; } }
	ul {
		ul {
			li {
				// list-style-type: circle;
				padding-left: 12px;
				border-left: 1px solid $turquoise; }
			margin-left: 2px; } } }

.footer {
	i {
		color: white; }
	color: white;
	padding: 3rem 1.5rem 3rem; }

@keyframes moveDown {
    0% {
        transform: translateY(-2%); }

    100% {
        transform: translateY(-100%); } }

@keyframes fadeIn {
    0% {
        opacity: 0%; }

    100% {
        opacity: 100%; } }


